    }

    .subwinwash,
    .subpwrwash  {
        order: 2;
    }
    
    .win-wash,
    .pwr-wash {
        order: 1;
    }

    /* quote slider */
    .slider {
        width: 400px;
        height: max-content;
        padding-top: 20px;
        padding-bottom: 20px;
    }

    .quote-slide span:first-child {
        width: 330px;
    }

    /* req img */
    .slider-title {
        font-size: 1.8rem;
    }

    .req-content-wrap {
        max-width: 100%;
        align-items: center;
    }

    /* LOWER INTRO WRAP */
    .intro-wrap {
        /* display: flex; */
        flex-direction: column;
        min-width: 330px;
        padding-bottom: 45px;
    }

    /* footer */
    .logo-foot-wrap {
        flex-direction: column;
        padding-top: 20px;
        padding-bottom: 30px;
    }

    .foot-contact-left {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-bottom: 30px;
    }

    #phone-number {
        margin: 0;
        padding: 5px;
    }

    #phone-number > a:active {
        color: rgba(219, 153, 36, 1);
    }

    .foot-links {
        padding-top: 5px;
        padding-bottom: 5px;
    }

    .footer-logo {
        display: block;
        aspect-ratio: 1.53 / 1;
        width: 90%;
        max-width: 400px;
        height: auto;
        border: 1px solid rgba(234, 179, 8, .9);
        border-radius: 5px;
        object-fit: contain;
    }

/* non-mobile-header */
    .top-main {
        display: none;
    }

 /* MOBILE NAV MENU */
    .mobile-header {
        position: absolute;
        top: 0;
        padding-top: 20px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        width: 100%;
    }

    .head-logo {
        width: 200px;
        height: 130px;
    }
}

@media screen and (width < 401px) { 
    .serv-main-quote {
        padding-top: 30px;
    }

    /* quote-slider */
    .slider-title {
        font-size: 1.7rem;
    }

    .slider {
        width: 300px;
        height: max-content;
        padding-top: 12px;
        padding-bottom: 12px;
    }

    .quote-slide span:first-child {
        width: 230px;
    }

    /* footer  */
    .foot-link > a {
        font-size: .8rem;
    }

    #phone-number {
        font-size: 2.1rem;
    }

    .foot-links {
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        gap: 9px;
    }
}

